import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableCaption,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CFormSelect,
  CButton,
  CForm,
  FormEvent,
} from '@coreui/react'
import { DocsCallout, DocsExample } from 'src/components'
import axios from 'axios'
const result = { result: 'dd' }

let theArray = [{ id: '', name: '' }]
const api = axios.create({
  baseURL: 'http://localhost:8080',
})

function SubmitTest(props) {
  api
    .post('/api/check', null, {
      params: {
        userId: props,
      },
    })
    //.then(({ data }) => setPosts(data))
    .then((res) => {
      //alert('첫번쨰 ' + res.data.test)
      result.result = res.data.test
      //setResult(res.data.test)
      //setPosts(res.data)
      //
      this.setPosts({ title: '롤롤롤' })
      return res.data.test
    })
    .catch((error) => {
      alert('There was an error!', error)
    })
}

class syscheck extends React.Component {
  constructor(props) {
    super(props)
    this.state = { value: 'test', test: '' }
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  handleChange(event) {
    this.setState({ value: event.target.value, test: event.target.test })
    this.handleChange = this.handleChange.bind(this)
  }
  handleSubmit(event) {
    SubmitTest(this.state.value)

    this.setState({ test: result.result })
    this.handleChange = this.handleChange.bind(this)
    event.preventDefault()
  }

  render() {
    return (
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <strong>System Check</strong> <small>was check</small>
            </CCardHeader>
            <CCardBody>
              <DocsExample>
                <CForm className="row gy-2 gx-3 align-items-center" onSubmit={this.handleSubmit}>
                  <CCol xs="auto">
                    <CFormSelect
                      id="autoSizingSelect"
                      value={this.state.value}
                      onChange={this.handleChange}
                    >
                      <option selected>Test</option>
                      <option>Prod</option>
                    </CFormSelect>
                  </CCol>
                  <CCol xs="auto">
                    <CButton type="submit">Submit</CButton>
                  </CCol>
                </CForm>
                <CCol xs="auto"></CCol>
                <CTable>
                  <CTableHead>
                    <CTableRow>
                      <CTableHeaderCell scope="col">{this.state.test}</CTableHeaderCell>
                      <CTableHeaderCell scope="col">{this.state.value}</CTableHeaderCell>
                      <CTableHeaderCell scope="col">{result.result}</CTableHeaderCell>
                      <CTableHeaderCell scope="col">ㅇㅇ</CTableHeaderCell>
                    </CTableRow>
                  </CTableHead>
                  <CTableBody>
                    <CTableRow>
                      <CTableHeaderCell scope="row">1</CTableHeaderCell>
                      <CTableDataCell>Mark</CTableDataCell>
                      <CTableDataCell>Otto</CTableDataCell>
                      <CTableDataCell>@mdo</CTableDataCell>
                    </CTableRow>
                    <CTableRow>
                      <CTableHeaderCell scope="row">2</CTableHeaderCell>
                      <CTableDataCell>Jacob</CTableDataCell>
                      <CTableDataCell>Thornton</CTableDataCell>
                      <CTableDataCell>@fat</CTableDataCell>
                    </CTableRow>
                    <CTableRow>
                      <CTableHeaderCell scope="row">3</CTableHeaderCell>
                      <CTableDataCell colSpan="2">Larry the Bird</CTableDataCell>
                      <CTableDataCell>@twitter</CTableDataCell>
                    </CTableRow>
                  </CTableBody>
                </CTable>
              </DocsExample>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    )
  }
}
export default syscheck
